const validate = (formState, setFormState) => {
  let isError = false;
  const errors = [];
  const dateRegex = /^(19[2-9][0-9]|20[0-2][0-9])[-/.](0?[1-9]|1[0-2])[-/.](0?[1-9]|[12][0-9]|3[01])$/gim;

  //name validation
  if (formState.name.length < 3) {
    isError = true;
    errors.nameError = "Imi za krtkie";
  } else if (formState.name.length > 18) {
    isError = true;
    errors.nameError = "Imi za dugie. Max 18 znakw.";
  } else {
    isError = false;
    errors.nameError = "";
  }

  //lastName validation
  if (formState.lastName.length < 3) {
    isError = true;
    errors.lastNameError = "Nazwisko za krtkie.";
  } else if (formState.lastName.length > 18) {
    isError = true;
    errors.lastNameError = "Nazwisko za dugie. Max 18 znakw.";
  } else {
    isError = false;
    errors.lastNameError = "";
  }

  //birthDate validation
  if (!dateRegex.test(formState.birthDate)) {
    isError = true;
    errors.birthDateError = "Niepoprawy format daty";
  }

  setFormState({ ...formState, ...errors });
  return isError;
};

export default validate;



